<template>
	<div class="bind">
		<div class="container">
			<div class="form-group">
				<label for="">手机号</label>
				<input type="number" @input="validateBtn" placeholder="请输入手机号" v-model="tel">
				<div class="getcode">获取验证码</div>
			</div>
			<div class="form-group">
				<label for="">验证码</label>
				<input type="number" @input="validateBtn" placeholder="请输入6位短信验证码" v-model="code">
			</div>
		</div>
		<div class="btn">
			<input :class="disabled?'sure disabled':'sure'" 
			:disabled="!disabled"
			type="button" value="确定">
		</div>
		<br><br><br><br><br><br><br>
		<router-link to="/list">列表</router-link>
	</div>
</template>
<script>
	export default {
		data(){
			return {
				tel:null,
				code:null,
				disabled:true
			}
		},
		methods:{
			validateBtn () {
				if(this.tel&&this.code) {
					this.disabled = false
				}
			}
		}
	}
</script>
<style lang="less" scoped>
@import '../../style/mixin.less';
	.container{
		background-color:#fff;
		margin:.75rem 0;
		padding-left:.75rem;
		.sc(.7rem,#000);
		.form-group {
			line-height: 1.8rem;
			position: relative;
			label {
				display: inline-block;
				.wh(3.5rem,100%);
			}
			input {
				height:100%;
				.sc(.7rem,#000);
			}
			.getcode {
				display: inline-block;
			    height: 45px;
			    margin-left: 5px;
			    padding: 0 0.6em 0 0.7em;
			    border-left: 1px solid #E5E5E5;
			    line-height: 45px;
			    vertical-align: middle;
			    font-size: 17px;
			    color: #3CC51F;
			}

			&:first-child:before {
				content: " ";
			    position: absolute;
			    left: 0;
			    right: 0;
			    bottom: 0;
			    height: 1px;
			    border-top: 1px solid #e5e5e5;
			    color: #e5e5e5;
			    -webkit-transform-origin: 0 0;
			    transform-origin: 0 0;
			    -webkit-transform: scaleY(0.5);
			    transform: scaleY(0.5);
			    z-index: 2;
			}
		}
	}
	.btn{
		padding: 0 .75rem;
		height:2.3rem;
		
		
	}
	.sure {
		.wh(100%,100%);
		background: #1AAD19;
		.sc(.9rem,#fff);
	}
	.disabled {
		background: #9ED99D;
		color:rgba(255, 255, 255, 0.6);
	}

</style>


